}

.Header {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100dvw;
  min-height: 60px;
  background-color: #2A52B0;
  border: solid 2px #FEFAFA;
  border-radius: 0px 0px 10px 10px;
}
.Header .HeaderTop {
  width: 100%;
  height: 60px;
  display: flex;
