}

.MenuBtn {
  height: 30px;
  width: 30px;
  margin-left: 4vw;
}

.SearchBtn {
  margin-right: 4vw;
  height: 25px;
  width: 25px;
}

.SearchBar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  height: 60px;
  width: 100%;
  background-color: #FEFAFA;
  display: none;
}
.SearchBar input,
.SearchBar .SearchEnter {
