}

.LoginAndSignup {
  position: relative;
  max-width: 850px;
  width: 90%;
  background: rgb(255, 255, 255);
  padding: 40px 30px;
  box-shadow: 1px 5px 10px rgba(44, 43, 43, 0.8);
  perspective: 2800px;
  background-color: rgb(254, 250, 250);
  border-radius: 20px;
}
.LoginAndSignup .cover {
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  width: 50%;
  background: red;
  z-index: 98;
  transition: all 1s ease;
  transform-origin: left;
  transform-style: preserve-3d;
}
.LoginAndSignup .cover::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background: #FEFAFA;
  opacity: 0.5;
  z-index: 100;
}
.LoginAndSignup .cover::after {
  transform: rotateY(180deg);
}
.LoginAndSignup .cover img {
  position: absolute;
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  z-index: 12;
  backface-visibility: hidden;
}
.LoginAndSignup .cover .back .backimage {
  transform: rotateY(180deg);
}
.LoginAndSignup .cover .text {
  position: absolute;
  z-index: 111;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.LoginAndSignup .cover .text .text-1,
.LoginAndSignup .cover .text .text-2 {
  font-size: 26px;
  font-weight: 600;
  color: #FEFAFA;
  text-align: center;
  backface-visibility: hidden;
}
.LoginAndSignup .cover .back .text .text-1,
.LoginAndSignup .cover .back .text .text-2 {
  transform: rotateY(180deg);
}
.LoginAndSignup .cover .text .text-2 {
  font-size: 15px;
  font-weight: 500;
}
.LoginAndSignup #flip:checked ~ .cover {
  transform: rotateY(-180deg);
}

.back .text .text-1,
.back .text .text-2 {
  transform: rotateY(180deg);
}

.LoginAndSignup form {
  height: 100%;
  width: 100%;
  background: #2A52B0;
}

.LoginAndSignup .form-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-content .login-form,
.form-content .signup-form {
  width: calc(50% - 25px);
  background-color: rgb(254, 250, 250);
}

form .form-content .title {
  position: relative;
  font-size: 24px;
  font-weight: 500;
  color: #333;
}

.LoginAndSignup form .form-content .signup-form .title {
  position: relative;
  font-family: "Brush Scipt MT", cursive;
  font-size: 20px;
  font-weight: 500;
  color: #333;
  text-align: center;
}

.LoginAndSignup form .form-content .signup-form .style {
