  font-size: 14px;
  color: #333;
  text-align: center;
  font-family: "Brush Scipt MT", cursive;
}

.LoginAndSignup form .form-content .signup-form .text2 {
  font-size: 14px;
  font-weight: 500;
  color: #333;
  text-align: center;
}

form .signup-form .title:before {
