}

form .form-content .input-boxes {
  margin-top: 30px;
}

form .form-content .input-box {
  display: flex;
  align-items: center;
  height: 45px;
  width: 100%;
  margin: 10px 0;
  position: relative;
}

.form-content .input-box input {
  height: 100%;
  width: 100%;
  outline: none;
  border: none;
  padding: 0 20px;
  font-size: 16px;
  font-weight: 500;
  border-bottom: 2px solid #2A52B0;
  transition: all 0.3s ease;
}

.form-content .input-box input:focus .form-content .input-box input:valid {
