  justify-content: center;
  align-items: center;
  color: #FEFAFA;
  margin-top: 40px;
  background-color: #2A52B0;
  border-color: #FEFAFA;
  border-radius: 7px;
  align-content: center;
}

form .form-content div .button:hover {
  color: #FEFAFA;
  margin-top: 40px;
  background-color: #2A52B0;
  border-color: #2A52B0;
  border-radius: 7px;
