@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700&family=Varela+Round&display=swap');

// Preset Styles
@mixin dja(){
    display: flex;
    justify-content: center;
    align-items: center;
}
@mixin dj(){
    display: flex;
    justify-content: center;
}
@mixin da(){
    display: flex;
    align-items: center;
}

@mixin EventTypeIcon(){
    height: 28px;
    min-width: 28px;
}
@mixin Input(){
    font-size: 22px;
    color: rgba(0, 0, 0, 0.26);
    border: solid 1px $GeneralColour;
    border: solid 2px $GeneralColour2;
    background: $GeneralColour;
    border: solid 2px $GeneralColour2;
}

// Variables
$headerHeight: 60px;
$CourseBoxHeight: 150px;
$PadNdGap: 4vw;
$GeneralColour: #FEFAFA;
$GeneralColourDim: rgb(44, 43, 43, 0.8);
$GeneralColour2: #2A52B0;

// General
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Varela Round';
}
p{
    color: $GeneralColour2;
}
a{
    color: $GeneralColour2;
    cursor: pointer;
    text-decoration: none;
    &:hover, &:visited{
        cursor: pointer;
        text-decoration: none;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    }
}
body, #root, .App{
    min-height: 100dvh;
}
main{
    @include dja();
    flex-direction: column;
    min-height: calc(100dvh - $headerHeight);
    background-color: $GeneralColour;
}
svg{
    color: $GeneralColour;
}
h1, h2, h3, h4, h5, h6{
    color: $GeneralColour2
}



.All{
    @include da();
    flex-direction: column;
    height: calc(100dvh - $headerHeight);
    overflow-y: scroll;
    width: 100dvw;
}
.Courses,
.CoursePage,
.CourseNotePage{
    > h1{
        color: $GeneralColour2;
    }
}

// Extra
div .Show{
    display: flex;
}


// Header
.Header{
    @include da();
    flex-direction: column;
    width: 100dvw;
    min-height: $headerHeight;
    background-color: $GeneralColour2;
    border: solid 2px $GeneralColour;
    border-radius: 0px 0px 10px 10px;
    .HeaderTop{
        width: 100%;
        height: $headerHeight;
        @include da();
        justify-content: space-between;
        svg{
            color: $GeneralColour;
        }
    }
}
.MenuBtn{
    height: 30px;
    width: 30px;
    margin-left: $PadNdGap;
}
.SearchBtn{
    margin-right: $PadNdGap;
    height: 25px;
    width: 25px;
}
.SearchBar{
    @include dja();
    gap: 5px;
    height: $headerHeight;
    width: 100%;
    background-color: $GeneralColour;
    display: none;
    input,
    .SearchEnter{
        height: 40px;
    }
    input{
        border-radius: 20px 0px 0px 20px;
        width: 250px;
        border: solid 1px $GeneralColour2;
        outline: none;
        padding: 0px 15px;
        font-size: 16px;
        color: #2A52BE;
    }
    .SearchEnter{
        @include dja();
        background-color: $GeneralColour2;
        border-radius: 0px 20px 20px 0px;
        width: 50px;
        color: $GeneralColour;
    }
}


// Login & Signup Page
.LoginAndSignup{
    position: relative;
    max-width: 850px;
    width: 90%;
    background: rgb(255, 255, 255);
    padding: 40px 30px;
    box-shadow: 1px 5px 10px $GeneralColourDim;
    perspective: 2800px;
    background-color: rgb(254, 250, 250);
    border-radius: 20px;
    .cover{
        position: absolute;
        top: 0;
        left: 50%;
        height: 100%;
        width: 50%;
        background: red;
        z-index: 98;
        transition: all 1s ease;
        transform-origin: left;
        transform-style: preserve-3d;
        &::before{
            content: '';
            position: absolute;
            height: 100%;
            width: 100%;
            background: $GeneralColour;
            opacity: 0.5;
            z-index: 100;
        }
        &::after{
            transform: rotateY(180deg);
        }
        img{
            position: absolute;
            height: 100%;
            width: 100%;
            object-fit: cover;
            z-index: 12;
            backface-visibility: hidden;
        }.back .backimage{
            transform: rotateY(180deg);
        }
        .text{
            position: absolute;
            z-index: 111;
            height: 100%;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }.text .text-1,
        .text .text-2{
            font-size: 26px;
            font-weight: 600;
            color: $GeneralColour;
            text-align: center;
            backface-visibility: hidden;
        }
        .back .text .text-1,
        .back .text .text-2{
            transform: rotateY(180deg);
        }.text .text-2{
            font-size: 15px;
            font-weight: 500;
        }
    }
    #flip:checked ~ .cover{
        transform: rotateY(-180deg);
    }
}
.back .text .text-1,
.back .text .text-2{
    transform: rotateY(180deg);
}
.LoginAndSignup form{
    height: 100%;
    width: 100%;
    background: $GeneralColour2;
}
.LoginAndSignup .form-content{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.form-content .login-form,
.form-content .signup-form{
    width: calc(100% / 2 - 25px);
    background-color: rgb(254, 250, 250);
}
form .form-content .title{
    position: relative;
    font-size: 24px;
    font-weight: 500;
    color: #333;
}
.LoginAndSignup form .form-content .signup-form .title{
    position: relative;
    font-family: 'Brush Scipt MT', cursive;
    font-size: 20px;
    font-weight: 500;
    color: #333;
    text-align: center;
}
.LoginAndSignup form .form-content .signup-form .style{
    background: $GeneralColour;
    border-radius: 5px;
    padding: 2px;
    width: auto;
    height: auto;
    align-content: center;
}
.LoginAndSignup form .form-content .signup-form .style #p{
    position: relative;
    font-size: 14px;
    color: #333;
    text-align: center;
    font-family: 'Brush Scipt MT', cursive;
}
.LoginAndSignup form .form-content .signup-form .text2{
    font-size: 14px;
    font-weight: 500;
    color: #333;
    text-align: center;
}
form .signup-form .title:before{
    width: 20px;
}
form .form-content .input-boxes{
    margin-top: 30px;
}
form .form-content .input-box{
    display: flex;
    align-items: center;
    height: 45px;
    width: 100%;
    margin: 10px 0;
    position: relative;
}
.form-content .input-box input{
    height: 100%;
    width: 100%;
    outline: none;
    border: none;
    padding: 0 20px;
    font-size: 16px;
    font-weight: 500;
    border-bottom: 2px solid $GeneralColour2;
    transition: all 0.3s ease;
}
.form-content .input-box input:focus
.form-content .input-box input:valid{
    border-color: $GeneralColour;
}
.form-content .input-box i{
    position: absolute;
    color: $GeneralColour;
    font-size: 16px;
}
form .form-content .text{
    font-size: 14px;
    font-weight: 500;
    color: #333;
}
form .form-content .text a{
    text-decoration: none;
}
form .form-content .text a:hover{
    text-decoration: underline;
}
form .form-content .button{
    color: $GeneralColour;
    margin-top: 40px;
}
form .form-content div .button{
    @include dja();
    color: $GeneralColour;
    margin-top: 40px;
    background-color: $GeneralColour2;
    border-color: $GeneralColour;
    border-radius: 7px;
    align-content: center;
}
form .form-content div .button:hover{
    color: $GeneralColour;
    margin-top: 40px;
    background-color: $GeneralColour2;
    border-color: $GeneralColour2;
    border-radius: 7px;
    align-content: center;
}
form .form-content .button input{
    color: $GeneralColour;
    background: $GeneralColour2;
    border-radius: 7px;
    padding: 0;
    cursor: pointer;
    transition: all 0.4s ease;
}
form .form-content .button input:hover{
    background: $GeneralColour2;
}
form .form-content label{
    @include dja();
    color: $GeneralColour2;
    cursor: pointer;
}
form .form-content label:hover{
    text-decoration: underline;
}
form .form-content .login-text,
form .form-content .sign-up-text{
    text-align: center;
    margin-top: 25px;
}
form .form-content .TC .TC:hover{
    text-decoration: none;
    color: $GeneralColour2;
}
form .form-content .TC{
    text-align: center;
}
.LoginAndSignup #flip{
    display: none;
}

// Welcome Page
.Welcome{
    a{
        color: $GeneralColour2;
    }
}

// Load App Welcome
.LoadAppWelcome{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100dvw;
    height: 100dvh;
    z-index: 999;
    background-color: $GeneralColour;
    @include dja();
    flex-direction: column;
    .LoadAppWelcomeDiv{
        @include dja();
        width: calc(100vw - 10px);
        height: calc(100vh - 10px);
        border: solid 0.5px $GeneralColour2;
    }
    *{
        color: $GeneralColour2;
    }
    .PantheraLogo{
        height: 200px;
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }
    .SponsorRecognision{
        position: absolute;
        bottom: 0px;
        @include dja();
        flex-direction: column;
        margin-bottom: 20px;
    }
    h1{
        font-family: 'Cinzel Decorative';
    }
}


