// Dashboard


.Dashboard{
    @include da();
    flex-direction: column;
    width: 100dvw;
    margin-top: 20px;
    h1, h2, h3, h4, h5{
        color: $GeneralColour2;
    }
}
.DashboardLinkCont{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $PadNdGap;
    padding: $PadNdGap;
    width: 100dvw;
}
.DashboardLink{
    height: 150px;
    background-color: $GeneralColour;
    border: solid 2px $GeneralColour2;
    border-radius: 10px;
    @include da();
    flex-direction: column;
    justify-content: center;
    svg{
        padding-top: 9px;
        opacity: 0.85;
    }
    .NoOfCourses{
        height: 90px;
        color: $GeneralColour2;
    }
    h1, h2, h3, h4, h5{
        color: $GeneralColour2;
        padding: 7px;
    }
}
.DashboardProfileImg{
    height: 85px;
    width: 85px;
    border-radius: 50%;
    object-fit: cover;
    border: solid 2px $GeneralColour2;
    img{
        position: relative;
        height: 100%;
        width: 100%;
        border-radius: 50%;
    }
}

// EditCourses
.EditCourses{
    @include dja();
    flex-direction: column;
    width: 100dvw;
    &>p, &>h1{
        padding: 0px $PadNdGap;
    }
    .ECCourses{
        @include da();
        flex-direction: column;
        width: 100%;
        padding: $PadNdGap;
        .Course{
            min-height: 70px;
            max-height: 100px;
            margin: $PadNdGap 0px 0px;
        }
        .Descrip{
            padding: $PadNdGap;
        }
    }
}

// Courses
.Courses{
    @include da();
    flex-direction: column;
    p{
        color: $GeneralColour2;
    }
}
.CourseList{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $PadNdGap;
    padding: $PadNdGap;
    width: 100dvw;
    a{
        color: #000;
        text-decoration: none;
    }
}

.Course{
    width: 100%;
    background-color: $GeneralColour;
    border-radius: 10px;
    min-height: $CourseBoxHeight;
    max-height: calc($CourseBoxHeight + 20px);
    transition: all 0.3s linear;
    border: solid 2px $GeneralColour2;
    .CourseDescrip{
        padding: 15px;
        h2,
        p{
            color: $GeneralColour2;
        }
    }
    &:hover{
        width: 106%;
        margin: -8px 0px 0px -3%;
        // height: 10c;
    }
}


// Course Page
.CoursePage{
    display: flex;
    flex-direction: column;
    gap: $PadNdGap;
    padding: 0px $PadNdGap $PadNdGap $PadNdGap;
    width: 100vw;
    height: calc(100dvh - $headerHeight);
    h1{
        justify-self: center;
    }
}
.CourseDocType{
    background-color: $GeneralColour2;
    height:180px;
    border-radius: 15px;
}
.CourseDocTypeDetails{
    padding: 20px 10px 10px 10px;
}


// CourseNotePage
.CourseNotePage{
    @include da();
    flex-direction: column;
    height: calc(100dvh - $headerHeight);
}
.CourseNotesDiv{
    width: 100dvw;
    display: grid;
    gap: $PadNdGap;
    padding: $PadNdGap;
    grid-template-columns: repeat(2, 1fr);
}
.CourseNote{
    height: $CourseBoxHeight;
    border-radius: 10px 10px 0px 0px;
    grid-column: span 1;
    background-color: $GeneralColour2;
    box-shadow: 0.5px 0.5px 5px $GeneralColour2;
    a:hover{
        color: $GeneralColour2;
    }
}
.CourseNoteName{
    height: calc($CourseBoxHeight - 53px);
}
.CourseNoteDetails{
    @include dja();
    gap: 6px;
    height: 55px;
    margin: 0px 0px 2px 0px;
    border-radius: 10px 10px 0px 0px;
    background-color: $GeneralColour;
    color: $GeneralColour2;
    svg{
        color: $GeneralColour2;
    }
}


// Timetable
// Timetable
// Timetable

.DefaultBody{
    @include da();
    flex-direction: column;
    width: 95%;
    height: calc(100dvh - $headerHeight);
}
.Home{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(17, 1fr);
    gap: 9px;
    margin: 10px 0px 0px 0px;
}
.HomeLeft{
    @include dja();
    flex-direction: column;
    grid-column: span 3;
    height: 68px;
    border-radius: 10px 0px 0px 10px;
    background-color: $GeneralColour;
    border: solid 2px $GeneralColour2;
    h1{
        font-weight: 500;
        font-size: 25px;
    }
}
.HomeRight{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    grid-column: span 14;
    min-height: 60px;
    border-radius: 0px 10px 10px 0px;
    background-color: $GeneralColour;
    border: solid 2px $GeneralColour2;
    h1{
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;
        margin-top: -4px;
    }
    h2{
        font-weight: 500;
        font-size: 20px;
    }
    h3{
        font-weight: 500;
        font-size: 17px;
        margin-left: 10px;
    }
}
.DefaulEventRightCont{
    margin: 10px;
    width: calc(100% - 20px);
    .HomeHid{
        display: none;
    }
}
.HomeRightEventTypeIcon{
    @include EventTypeIcon();
    float: right;
}
.HomeRightCourseLocation,
.HomeRightCourseTime{
    @include da();
    color: $GeneralColour2;
    svg{
        color: $GeneralColour2;
    }
}
.HomeRightCourseLocationIcon,
.HomeRightCourseTimeIcon{
    height: 20px;
    margin-top: 5px;
}


