// Edit Courses
.EditCoursesLink{
    position: absolute;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    @include dja();
    background-color: $GeneralColour;
    border: solid 2px $GeneralColour2;
    bottom: $PadNdGap;
    right: $PadNdGap;
    z-index: 900;
    svg{
        color: $GeneralColour2;
    }
}



@media (max-width: 730px){
    .LoginAndSignup .cover{
        display: none;
    }
    .form-content .login-form,
    .form-content .signup-form{
    width: 100%;
    }
    .form-content .signup-form{
        display: none;
    }
    .LoginAndSignup #flip:checked ~ form .signup-form{
        display: block;
    }
    .LoginAndSignup #flip:checked ~ form .login-form{
        display: none;
    }
}

@media (min-width: 1100px),
((min-width: 700px) and (max-height: 450px)),
((min-width: 450px) and (max-height: 700px)){
    $PadNdGap: 2vw;
    .CourseList,
    .CourseNotesDiv{
        grid-template-columns: repeat(3, 1fr);
        gap: $PadNdGap;
        padding: $PadNdGap;
    }
    .MenuBtn{
        margin-left: $PadNdGap;
    }
    .SearchBtn{
        margin-right: $PadNdGap;
    }
    // CoursePage
    .CoursePage{
        grid-template-columns: repeat(2, 1fr);
        // padding: 10vw;
        &> h1{
            grid-column: span 2;
            font-size: 40px;
        }
    }
}
@media ((min-width: 700px) and (max-height: 450px)){
    $CourseBoxHeight: 190px;
    $PadNdGap: 2.5vw;
    .CourseList,
    .CourseNotesDiv{
        gap: $PadNdGap;
        padding: $PadNdGap;
    }
    .CourseNote{
        height: $CourseBoxHeight;
    }
    .CourseNoteName{
        height: calc($CourseBoxHeight - 63px);
    }
    .CourseNoteDetails{
        height: 64px;
    }
}

// Tab
@media (min-width: 700px){
    $CourseBoxHeight: 240px;
    $PadNdGap: 5vw;
    .CourseList,
    .CourseNotesDiv{
        gap: $PadNdGap;
        padding: $PadNdGap;
    }
    .CourseNotePage{
        h1{
            margin-top: 25px;
            font-size: 45px;
        }
    }
    .CourseNote{
        height: $CourseBoxHeight;
    }
    .CourseNoteName{
        height: calc($CourseBoxHeight - 83px);
    }
    .CourseNoteDetails{
        height: 84px;
        font-size: 23px;
        gap: 15px;
    }
}

// Lappi
@media (min-width: 1500px){
    $CourseBoxHeight: 240px;
    $PadNdGap: 2.5vw;

    .All{
        &::-webkit-scrollbar{
            display: none;
        }
    }
    
    // Dashboard
    .DashboardLinkCont{
        width: 80vw;
        padding: 1.5vw 0px;
    }



    .CourseList,
    .CourseNotesDiv{
        width: 80vw;
        grid-template-columns: repeat(5, 1fr);
        gap: $PadNdGap;
        padding: $PadNdGap;
    }
    .CourseNotePage{
        h1{
            margin-top: 30px;
            font-size: 50px;
        }
    }
    .CourseNote{
        height: $CourseBoxHeight;
    }
    .CourseNoteName{
        height: calc($CourseBoxHeight - 83px);
    }
    .CourseNoteDetails{
        height: 84px;
        font-size: 23px;
        gap: 15px;
    }


    // CoursePage
    .CoursePage{
        grid-template-columns: repeat(3, 1fr);
        width: 80vw;
        &> h1{
            grid-column: span 3;
            font-size: 55px;
        }
    }
    .CourseDocType{
        height: calc($CourseBoxHeight * 1.3);
        .CourseDocTypeDetails{
            padding: 40px 20px 20px 20px;
        }
    }

}

@media (max-width: 330px){
    .All{
        &::-webkit-scrollbar{
            display: none;
        }
    }
    $CourseBoxHeight: 150px;
    $PadNdGap: 2.5vw;
    .CourseList{
        .Course{
            grid-column: span 2;
        }
    }
    .CourseDocType{
        height: $CourseBoxHeight;
    }
}